body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
}

#app.legacy-center a {
  color: #646cff;
  -webkit-text-decoration: inherit;
  -webkit-text-decoration: inherit;
  text-decoration: inherit;
  font-weight: 500;
}

#app.legacy-center a:hover {
  color: #535bf2;
}

body:has(#app.legacy-center) {
  place-items: center;
  display: flex;
}

#app.legacy-center {
  text-align: center;
  max-width: 1280px;
  margin: 0 auto;
  padding: 2rem;
}

#htmx-result {
  padding: 2rem 0;
}

.logo {
  will-change: filter;
  filter: drop-shadow(0 0 2em #f3c077aa);
  height: 12em;
  padding: 2em;
  transition: filter .3s;
  display: inline;
}

.content {
  padding: 2em;
}

.read-the-docs {
  color: #888;
}

#app.legacy-center button {
  color: #fefef5;
  cursor: pointer;
  background-color: #1a1a1a;
  border: 1px solid #0000;
  border-radius: 8px;
  padding: .6em 1.2em;
  font-family: inherit;
  font-size: 1em;
  font-weight: 500;
  transition: border-color .25s;
}

#app.legacy-center button:hover {
  border-color: #646cff;
}

#app.legacy-center button:focus, #app.legacy-center button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

[x-cloak] {
  display: none !important;
}

@keyframes errorPageFadeIn {
  0% {
    opacity: 0;
    transform: translateY(.5rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.error-fade-in {
  opacity: 0;
  animation: .4s ease-out forwards errorPageFadeIn;
}

@keyframes rating {
  0%, 40% {
    filter: brightness(1.05) contrast(1.05);
    scale: 1.1;
  }
}

@keyframes dropdown {
  0% {
    opacity: 0;
  }
}

@keyframes radio {
  0% {
    padding: 5px;
  }

  50% {
    padding: 3px;
  }
}

@keyframes toast {
  0% {
    opacity: 0;
    scale: .9;
  }

  to {
    opacity: 1;
    scale: 1;
  }
}

@keyframes rotator {
  89.9999%, to {
    --first-item-position: 0 0%;
  }

  90%, 99.9999% {
    --first-item-position: 0 calc(var(--items) * 100%);
  }

  to {
    translate: 0 -100%;
  }
}

@keyframes skeleton {
  0% {
    background-position: 150%;
  }

  to {
    background-position: -50%;
  }
}

@keyframes menu {
  0% {
    opacity: 0;
  }
}

@keyframes progress {
  50% {
    background-position-x: -115%;
  }
}

